import React, {useState} from 'react';
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

import "../assets/sass/login.sass";

export const LoginPage = (props) => {

  const [data, setData] = useState({password:'', email:''});

  const handleChange = e => {
    setData({...data, [e.target.name]: e.target.value })
  }

  // const handleKeyPress = e => {
  //   if (e.keyCode === 13 || e.which === 13) {
  //     isButtonDisabled || handleSubmit();
  //   }
  // }

  const loginForm = (props) => {
    return <Form onSubmit={(e) => props.onSubmit(e, data)}>
      <Form.Group controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" name="email" placeholder="Enter email"  value={data.email} onChange={(e) => handleChange(e)}  />
        <Form.Text className="text-muted">
          We'll never share your email with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control type="password" name="password"  placeholder="Password" value={data.password} onChange={(e) => handleChange(e)} />
      </Form.Group>
      <Button variant="primary" type="submit" className="login-btn">
        Submit
      </Button>
    </Form>
  }

  return (
    <div className="container">
      <div className="d-flex justify-content-center h-100">
        <div className="card zoomInUp animated">
          <div className="card-header">
            {loginForm(props)}
          </div>
        </div>
      </div>
    </div>
  );
}
