import React, { useState, useEffect } from 'react'
import Spinner from 'react-bootstrap/Spinner'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Card from 'react-bootstrap/Card'
import { withRouter } from 'react-router-dom'

import { LinkContainer } from "react-router-bootstrap"
import { fetchEvents } from '../../api'

const EventsIndexPage = (props) => {

  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchEvents().then((response)=>{
      console.log(response.data)
      setData(response.data.events)
      setIsLoading(false)
    })
   }, []);

  const handleAddCourse = () => {
    props.history.push('events/create')
  }

  /*
  const list = () => {
    return data.map((item)=>{
      return (
        <ul>
          <li>
        <LinkContainer to={`/events/${item.id}`}>
          <h2 key={item.id}>{item.title}</h2>
        </LinkContainer>
          </li>
        </ul>
      )
    })
  }
  */

  const listAsCards = () => {
    return data.map((item)=>{
      return (
        <Card style={{marginTop:'4px'}}>
        <Card.Header>{item.title}</Card.Header>
        <Card.Body>
          {/* <Card.Title>{item.title}</Card.Title> */}
          <Card.Text>
            {item.description}
          </Card.Text>
          <LinkContainer to={`/events/${item.id}`}>
            <Button variant="primary">mehr ...</Button>
          </LinkContainer>
        </Card.Body>
        {/* <Card.Footer className="text-muted">2 days ago</Card.Footer> */}
      </Card>
      )
    })
  }

  return (!isLoading) ?
    <Container  style={{marginTop:'2em'}}>
    <Row>
      <Col>
        <h1>Termine</h1>
        {((sessionStorage.getItem('admin_role')) && (
          <><Button type="button" variant="primary" onClick={() => { handleAddCourse() }}>Add</Button>
          {listAsCards()}
          </>
        )) || (
          listAsCards()
        )}
        </Col>
      </Row>
    </Container>
  : <Spinner animation="border" role="status">
  <span className="sr-only">Loading...</span>
 </Spinner>
}

export default withRouter(EventsIndexPage)
